const UserEdit = () => {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [cookies] = useCookies(["Authorization"]);
  const [basicInfo, setBasicInfo] = useState([]);
  const [deliveryInfo, setDeliveryInfo] = useState([]);

  const getUserInfo = async () => {
    try {
      const response = await api.get("user", {
        headers: {
          Authorization: cookies.Authorization,
        },
        params: {
          detail: true,
        },
      });
      setInfo(response.data);
    } catch (e) {
      setError("    .");
    } finally {
      setLoading(false);
    }
  };

  const setInfo = (info) => {
    setBasicInfo([
      { type: "", content: info.name ? info.name : null },
      { type: "", content: info.userID && info.userID },
      {
        type: "",
        content: info.phoneNumber ? info.phoneNumber : null,
        changeLink: true,
      },
      { type: "", changeLink: "./changepw" },
      {
        type: "",
        content:
          info.account.accountName &&
          info.account.accountNumber &&
          info.account.accountOwner
            ? `${info.account.accountName} ${info.account.accountNumber} ${info.account.accountOwner}`
            : null,
        changeLink: true,
      },
    ]);
    setDeliveryInfo([
      {
        type: "",
        content: info.address.addressRecipient
          ? info.address.addressRecipient
          : null,
        changeLink: true,
      },
      {
        type: "",
        content: info.address.addressPhoneNumber
          ? info.address.addressPhoneNumber
          : null,
        changeLink: true,
      },
      {
        type: " ",
        content: info.address.address ? info.address.address : null,
        changeLink: true,
      },
      {
        type: "",
        content: info.address.addressDetail ? info.address.addressDetail : null,
        changeLink: true,
      },
      {
        type: "",
        content: info.address.addressZipCode
          ? info.address.addressZipCode
          : null,
        changeLink: true,
      },
    ]);
  };

  useEffect(() => {
    window.scrollTo(0, 0);
    getUserInfo();
  }, []);

  return (
    <Container>
      {loading ? (
        <LoadingContainer>
          <Loading />
        </LoadingContainer>
      ) : error ? (
        <LoadingContainer>
          <span>{error}</span>
        </LoadingContainer>
      ) : (
        <>
          <Title></Title>
          <EditContainer title="" infos={basicInfo} />
          <EditContainer title=" " infos={deliveryInfo} isDelivery />
        </>
      )}
    </Container>
  );
};

export default UserEdit;
